import React from 'react';
import { useSelector } from 'react-redux';
import { useHistory } from 'react-router-dom';
import { getListBannerSelector } from 'src/redux/selectors/common.selector';
import RecruitmentDetailScreen from './recruitmentDetail.view';

const RecruitmentDetailContainer = (props) => {
  const recruitment = props?.location?.state?.item;
  const listBanner = useSelector(getListBannerSelector);
  const history = useHistory();
  return (
    <RecruitmentDetailScreen
      listBanner={listBanner}
      recruitment={recruitment}
      onApplyRecruitmentClick={() => {
        history.push('/fill-recruitment');
      }}
    />
  );
};

export default RecruitmentDetailContainer;
